@import "../../styles/variables.scss";

.scroll-up-button {
    position: fixed;
    bottom: 20px;
    right: 20px;
    padding: 10px;
    background-color:$primaryColor;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  
    &:hover {
      background-color: $secondaryColor;
    }
  }
  
